.imageDiv {
  display: flex;
  overflow: hidden;
  width: 20vw;
  position: fixed;
}

@media only screen and (max-width: 600px) {
  .ageGroups {
    flex-direction: column;
    margin-top: 20px;
  }
  .ageText {
    margin-top: 0vh;
  }
  .ageGroupIndiv {
    width: 45vw;
    height: 15vh;
    margin-top: 20px;
  }
}
